import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from './../../../http/custom-operation/custom-operation.http.service';
import { Message } from 'primeng/primeng'
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { tableMessage } from 'app/@uisftech/common/constant/config';
// import { MarketingActivityBean } from '../marketing-activity-query/bean/MarketingActivity.bean';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { QryMyMarketBean } from './bean/qryMyMarket.bean';
/**
 * 我的营销
 */
@Component({
	selector: 'app-my-marketing',
	templateUrl: './my-marketing.component.html',
	styleUrls: ['./my-marketing.component.scss'],
	providers: [CusOperationHttpService, Commfunc]
})
export class MyMarketingComponent implements OnInit {
	// marketingActivityBean: MarketingActivityBean = new MarketingActivityBean();
	tabMesg: any = tableMessage;//表格中无数据时显示的内容
	startDate: string;
	endDate: string;
	calendar: any = [];
	dataList: any = [];
	totalNum: number;
	// pageSize: number = 10;
	msgs: Message[] = [];
	ch: any = CALENDAR_CN;
	marketingTimeFrom: Date = null;
	marketingTimeTo: Date = null;
	//机构树模态框
	orgTreeDisplay: boolean = false;//模态框是否展示
	treeOrgName: string = '';
	custMajorName: string;
	qryMyMarketBean: QryMyMarketBean = new QryMyMarketBean();
	status: any = [];
	code: any;
	WorkStatus: any = [];
	taskType: any = [];
	countNum: number = 10;
	pageNum: number = 1;
	first: number = 0;//分页控制
	constructor(
		private cusOperationHttpService: CusOperationHttpService,
		private commfunc: Commfunc,
		private router: Router,
	) {
		this.codeValues() //调用方法，获取全部码值
		this.status = this.code['MarketingStatus'];
		this.WorkStatus = this.code['WORKSTATUS'];//工作任务状态
		this.taskType = this.code['TASKTYPE'];//任务种类
	}

	//初始化
	ngOnInit() {
		this.qryMyMarketBean.countNum = 10;
		this.qryMyMarketBean.pageNum = 1;
		this.first = 0;
		this.check();
	}
	check () {
		this.queryClick();
	}
	//查询按钮
	queryClick() {
		this.qryMyMarketBean.marketingTimeFrom = this.commfunc.transDateN(this.marketingTimeFrom);
		this.qryMyMarketBean.marketingTimeTo = this.commfunc.transDateN(this.marketingTimeTo);
		let ob = this.cusOperationHttpService.queryMarketingCampaignUndoList(this.qryMyMarketBean);
		ob.subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.dataList = data.returnList;
				this.totalNum = data.totalCount;
				data.returnList.forEach(element => {
					if (element.taskType == '') {
						element.taskType = null;//任务种类
					}
					if (element.workStatus == '') {
						element.workStatus = null//工作任务状态
					}
					if (element.statusId == '') {
						element.statusId = null//营销活动状态
					}
				});
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message })
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" })
		})
	}
	//页码重置
	querySecleReset() {
		this.qryMyMarketBean.countNum = 10;
		this.qryMyMarketBean.pageNum = 1;
		this.first = 0;
	}
	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}

	//选择机构
	chooseOrg() {
		document.body.style.overflow = 'hidden';
		this.orgTreeDisplay = !this.orgTreeDisplay;
	}

	//机构树回调
	treeCall(param) {
		document.body.style.overflow = 'auto';
		this.orgTreeDisplay = false;
		this.custMajorName = param.orgName;
		this.qryMyMarketBean.releaseOrg = param.orgId;
	}
	//开始日期
	showDataStart() {
		if (this.marketingTimeFrom && this.marketingTimeTo) {
			if (this.marketingTimeFrom > this.marketingTimeTo) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
				setTimeout(() => {
					this.marketingTimeFrom = null;
				})
			}
		}
	}
	//结束日期
	showDataEnd() {
		if (this.marketingTimeFrom && this.marketingTimeTo) {
			if (this.marketingTimeFrom > this.marketingTimeTo) {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '开始日期不能大于结束日期' });
				setTimeout(() => {
					this.marketingTimeTo = null;
				})
			}
		}
	}
	//手动输入日期时，失去焦点时进行判断
	onchange1() {
		this.showDataStart()
	}
	onchange2() {
		this.showDataEnd()
	}

	//重置按钮
	reset() {
		this.qryMyMarketBean = new QryMyMarketBean();
		this.marketingTimeFrom = null;
		this.marketingTimeTo = null;
		this.custMajorName = '';
		this.querySecleReset();
	}

	//分页
	paginate(event) {
		//每页显示的条数
		this.qryMyMarketBean.countNum = Number(event.rows);
		//当前页
		let currentPage = event.page + 1;
		this.qryMyMarketBean.pageNum = currentPage;
		this.first = (currentPage - 1) * this.qryMyMarketBean.countNum;
		this.queryClick();
	}
	//按钮权限
	permissionCheck(id) {
		return this.commfunc.permissionCheck(id)
	}
	//点击处理按钮
	toDisposeSchedule(item) {
		if (item.taskType == '1002') {
			this.router.navigate(['/pages/tzb/custom/marketing/my-activite-project/deal-with', item])//营销人员跟进页面
		} else if (item.taskType == '1008') {
			this.router.navigate(['/pages/tzb/custom/marketing/my-activite-project/rask-move', item])//任务流转-分配人员
		}
	}
	//点击查看详情
	toDetail(item) {
		if (item.taskType == '1002') {
			this.router.navigate(['/pages/tzb/custom/marketing/my-activite-project/deal-with-detail', item]);
		} else if (item.taskType == '1008') {
			this.router.navigate(['/pages/tzb/custom/marketing/my-activite-project/rask-move-detail', item]);
		}
	}

}

