import { Component, OnInit, Output, Input, EventEmitter, OnChanges } from '@angular/core';
import { ConfirmDialogModule, ConfirmationService } from 'primeng/primeng';
import { Router } from '@angular/router';
import { Message } from 'primeng/primeng';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import * as moment from 'moment';
import { FollowUpBean } from './bean/follow-up.bean';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { tableMessage, CALENDAR_CN } from "app/@uisftech/common/constant/config";
import { TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
@Component({
  selector: 'follow-up',
  templateUrl: './follow-up.component.html',
  styleUrls: ['./follow-up.component.scss'],
  providers: [CusOperationHttpService, ConfirmationService, Commfunc]
})
export class FollowUpComponent implements OnInit, OnChanges {
  /**
   * 营销人员跟进列表的我要跟进
   */
  @Input() inValue: any;
  @Input() inValue_2: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  channelReleaseMsg: string;
  followUpBean: FollowUpBean = new FollowUpBean();
  msgs: Message[] = [];
  list: any;
  // lian: any[] = Lian;
  custNum: string;
  productList: any = [];
  custName: string;
  Phone: any[];
  phone: string;
  interestedDispaly: boolean = false;
  contactDateDispaly: boolean = false;
  ch: any = CALENDAR_CN;
  nextContactTime: any;
  // contactWay: any = ContactWay;
  undisplay = false;
  showModel: string;
  updateValue: any = [];
  isContactWay: boolean = false;
  isContactEquity: boolean = false;
  isWhetherInterested: boolean = false;
  isNextContactTime: boolean = false;
  headerTitle: string;
  contactWay: any = [];
  fileList: any = [];
  // display: boolean = false;
  DOWNLOAD: string = TZB_CUS_DOWNLOAD;//下载
  code: any;
  productName:string;
  temData: Date = new Date();
  constructor(
    private httpService: CusOperationHttpService,
    private router: Router,
    private confirmationService: ConfirmationService,
    private commfunc: Commfunc,
    private fb: FormBuilder,
  ) {
    this.codeValues() //调用方法，获取全部码值
    this.contactWay = this.code['CONTACTWAY'];//联系方式

  }

  //初始化
  ngOnInit() {

  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  ngOnChanges() {
    if (this.inValue) {
      this.channelReleaseMsg = this.inValue.paramFollow.channelReleaseMsg;
      this.productList = this.inValue.paramFollow.productList;
      // for (let i=0;i<this.productList.length;i++) {
      //   this.productName=this.productList[i].productName
      // }
      console.log(this.productName)
      this.custName = this.inValue.custName;
      this.phone = this.inValue.phone;
      this.followUpBean.custNo = this.inValue.custNo;
      this.followUpBean.marketingCampaignId = this.inValue.marketingCampaignId;
      this.followUpBean.workId = this.inValue.workId;
      this.fileList = this.inValue.marketingContentFileList;
    }
    this.nextContactTime = ''
  }
  //确定按钮
  comfirm() {
    //如果时间存在-转换
    if (this.nextContactTime) {
      this.followUpBean.nextContactTime = moment(this.nextContactTime).format("YYYY-MM-DD");
    }
    //联系方式-校验不为空
    if (this.followUpBean.contactWay == '' || this.followUpBean.contactWay == undefined || this.followUpBean.contactWay == null) {
      this.isContactWay = true;
      return
    } else {
      this.isContactWay = false;
    }
    //联系情况-校验不为空
    if (this.followUpBean.contactEquity == '' || this.followUpBean.contactEquity == undefined || this.followUpBean.contactEquity == null) {
      this.isContactEquity = true;
      return
    } else {
      this.isContactEquity = false;
    }
    //感兴趣
    if (this.followUpBean.whereInterested == '' || this.followUpBean.whereInterested == undefined || this.followUpBean.whereInterested == null) {
      this.isWhetherInterested = true;
      return
    } else {
      this.isWhetherInterested = false;
    }
    //需再次联系
    if (this.followUpBean.nextContactTime == '' || this.followUpBean.nextContactTime == undefined || this.followUpBean.nextContactTime == null) {
      this.isNextContactTime = true;
      return
    } else {
      this.isNextContactTime = false
    }
      
    let ob = this.httpService.marketingPersonnelDispose(this.followUpBean);
    ob.subscribe(data => {
      if (data.returnCode.code == 'success') {

        this.outValue.emit(false);

      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务异常" });
    });
  }

  updateCall(param) {
    this.undisplay = param;
  }
  //日期转换
  showData() {
    // this.followUpBean.nextContactTime = this.commfunc.transDate(this.nextContactTime);
    this.isNextContactTime = false
  }
  //下载按钮
  down(temp) {
  	let name = temp.fileName;
		let path = temp.fileUrl;
		open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(name) + "&fileUrl=" + path);
  }
}
