import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { Message } from 'primeng/primeng';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { TZB_CUS_DOWNLOAD } from "app/@uisftech/common/service/http.constant";
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { RaskMoveBean, DistributeBean, QueryUserBean } from './bean/rask-move.bean';
import * as _ from 'lodash';
/**
 * 任务流转
 */
@Component({
	selector: 'app-rask-move',
	templateUrl: './rask-move.component.html',
	styleUrls: ['./rask-move.component.scss'],
	providers: [CusOperationHttpService, Commfunc]
})
export class RaskMoveComponent implements OnInit {
	DOWNLOAD: string = TZB_CUS_DOWNLOAD;//下载
	tabMesg: any = tableMessage;//表格中无数据时显示的内容
	raskMoveBean: RaskMoveBean = new RaskMoveBean();
	distributeBean: DistributeBean = new DistributeBean();
	queryUserBean: QueryUserBean = new QueryUserBean();
	pageNum: number = 1;
	countNum: number = 10;
	userList: any = [];
	totalMessage: any = [];
	user: any;
	items: any = [];
	list: any = [];
	marketingCampaignId: string;
	msgs: Message[] = [];
	totalNum: number;
	userName: string;
	fileList1: any = [];
	fileList2: any = [];
	temp1 = {
		tellerId: '',
		tellerName: '',
		customerNum: '',
	}
	fpqk: any[];
	trueOrFalse: any;
	marketingProgramId: string;
	code: any;
	status: any = [];
	personPage: boolean = false;
	extParam: any;
	orgId: string;
	userInValue: string;
	personName: string;
	personNo: string;
	isPersonNo: Boolean = false;
	index: number;
	isSure:boolean = false;
	Property:boolean=false;
	constructor(
		private routeInfo: ActivatedRoute,
		private router: Router,
		private cusOperationHttpService: CusOperationHttpService,
		private commfunc: Commfunc,
	) {
		this.codeValues() //调用方法，获取全部码值
		this.status = this.code['MarketingStatus'];
		let param = this.routeInfo.snapshot.params['marketingCampaignId'];
		let ob = this.cusOperationHttpService.queryMarketingCampaignById({ marketingCampaignId: param })
		ob.subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.totalMessage = data;
				this.fileList1 = data.marketingContentFileList;//营销活动说明
				this.fileList2 = data.marketingTargetFileList;//营销目标
				this.marketingProgramId = data.marketingProgramId;
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})
		this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
		if (this.user.userId) {
			this.queryUserBean.userLoginId = this.user.userId;
		}
		this.queryUserBean.partyId = this.routeInfo.snapshot.params['userOrgId'];
		this.queryUserBean.postId = '1';
		this.queryUserBean.pageNum = 1;
		this.queryUserBean.countNum = 10;
	}

	//初始化
	ngOnInit() {
		this.commfunc.clearSession('flagIndex');
		this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
		if (this.user.extParam) {
			this.extParam = this.user.extParam;
			console.log(this.extParam)
		}
		if (this.routeInfo.snapshot.params['workId']) {
			this.raskMoveBean.workId = this.routeInfo.snapshot.params['workId'];
		} else if (this.routeInfo.snapshot.params['workEffortId']) {
			this.raskMoveBean.workId = this.routeInfo.snapshot.params['workEffortId'];
		}
		this.raskMoveBean.marketingCampaignId = this.routeInfo.snapshot.params['marketingCampaignId'];

		this.raskMoveBean.countNum = 10;
		this.raskMoveBean.pageNum = 1;
		this.table();
	}

	//营销人员分配客户统计信息查询
	table() {
		let ob = this.cusOperationHttpService.queryMarketingPersonnelDistribute(this.raskMoveBean);
		ob.subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.items = data;
				this.totalNum = data.totalNum;
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})
	}
	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}

	//选择员工
	chooseWorker(i) {
		console.log(event)
		document.body.style.overflow = 'hidden';
		this.personPage = true;
		this.orgId = this.extParam.orgId;
		this.userInValue = this.orgId;
		this.index = i;
	}

	//员工回调
	perInfor(item) {
		this.isSure = true;
		document.body.style.overflow = 'auto';
		this.personPage = false;
		if (item.length > 0) {
			for (let i of this.list) {
				if (!i.tellerName) {
					i.tellerId = item[0].tellerId;
					i.tellerName = item[0].tellerName;
					console.log(this.list)
					return;
				}
			}
			this.list.push(item[0])
		}
	}
	//确认并提交按钮
	comfirm() {
		this.distributeBean.marketingCampaignId = this.raskMoveBean.marketingCampaignId;
		this.distributeBean.workId = this.raskMoveBean.workId;
		let ch = JSON.parse(this.commfunc.getSessionDataCH('chName'));
		this.distributeBean.userName = ch['userName'];
		let car = [];
		this.list.forEach(ind => {
			let cars = {};
			cars['manageId'] = ind.tellerId;
			cars['name'] = ind.tellerName;
			cars['customerNum'] = ind.customerNum;
			car.push(cars);
		});
		this.distributeBean.distributeList = car;
		//联系人
		if (!this.isSure) {
			this.isPersonNo = true;
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择主联系人' });
			return
		} else {
			this.isPersonNo = false;
		}
		let ob = this.cusOperationHttpService.marketingPersonnelDistribute(this.distributeBean);
		ob.subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
		})
	}
	//点击增加btn
	doAdd() {
		this.Property=true;
		this.list.push(this.copyObj(this.temp1));
	}

	//删除一行
	reduce(i) {
		this.list.splice(i, 1);
	}

	/**
	* 对象拷贝
	*/
	copyObj(obj) {
		let temp = {};
		for (var i in obj) {
			temp[i] = obj[i]
		}
		return temp;
	}

	//分页
	paginate(event) {
		this.raskMoveBean.countNum = event.rows * 1;
		let currentPage = event.page + 1;
		this.raskMoveBean.pageNum = currentPage;
		this.table();
	}
	//点击页面营销方案中的查看详情按钮跳转营销方案详情页面
	btnDetail() {
		this.router.navigate(['/pages/tzb/custom/marketing/marketing-project/marketing-program-detail', { marketingProgramId: this.marketingProgramId }])
	}
	//返回上一页
	returnBtn() {
		setTimeout(() => {
			window.history.go(-1);
		}, 500)
	}
	//下载按钮
	down(temp) {
		let name = temp.fileName;
		let path = temp.fileUrl;
		open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(name) + "&fileUrl=" + path);
	}
}
