import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { DealWithComponent } from './deal-with/deal-with.component';
import { DealWithDetail } from './deal-with-detail/deal-with-detail.component';
import { MyMarketingComponent } from './my-marketing/my-marketing.component';
import { RaskMoveComponent } from './my-marketing/rask-move/rask-move.component';
import { RaskMoveDetailComponent } from './rask-move-detail/rask-move-detail.component';
export const routes: Routes = [
    {
        path: '',
        children: [
            {
                path: 'deal-with',
                component: DealWithComponent
            },
            {
                path: 'deal-with-detail',
                component: DealWithDetail
            },
            {
                path: 'my-marketing',
                component: MyMarketingComponent
            },
            {
                path: 'my-marketing',
                component: MyMarketingComponent
            },
            {
                path: 'rask-move',
                component: RaskMoveComponent
                
            },
            {
                path: 'rask-move-detail',
                component: RaskMoveDetailComponent                
            }
        ]
    }
]
export const MyProjectRouting: ModuleWithProviders = RouterModule.forChild(routes);