/**
 * 我的营销页面-查看详情按钮
 */
import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';
import { TZB_CUS_DOWNLOAD } from "app/@uisftech/common/service/http.constant";
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { DealWithBean } from '../deal-with/bean/deal-with.bean';


@Component({
	selector: 'deal-with-detail',
	templateUrl: './deal-with-detail.component.html',
	styleUrls: ['./deal-with-detail.component.scss'],
	providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})
export class DealWithDetail implements OnInit {
	dealWithBean: DealWithBean = new DealWithBean();
	tabMesg: any = tableMessage;//表格中无数据时显示的内容
	DOWNLOAD: string = TZB_CUS_DOWNLOAD;//下载
	title: string = "营销跟进任务";
	cars: any = [];
	channelReleaseMsg: string;
	headerTitle: string;
	display: boolean = false;
	inValue: any;
	data: any;
	items: any = [];
	items1: any = [];
	total: number;
	custNo: string;
	showModel: string;
	updateValue: any = [];
	msgs: Message[] = [];
	totalNum: number;
	marketingCampaignId: string;
	paramFollow: any;
	// followUpDeatilDisplay: boolean = false;
	marketingCampaignName: string = '';
	workId = '';
	fileList1: any = [];
	fileList2: any = [];
	totalMessage: any = [];
	marketingProgramId: string;
	prdAndPromRelList: any = [];
	status: any = [];
	code: any;
	sexOpt: any = [];
	followStatus: any = [];
	trueOrFalse: any = new Array(1);//折叠面板收起、展开
	productList: any = [
		{
			productId: '',
			productName: '',
		}
	]
	constructor(
		private httpService: CusOperationHttpService,
		private routeInfo: ActivatedRoute,
		private commfunc: Commfunc,
		private router: Router,
		private confirmationService: ConfirmationService
	) {
		this.codeValues() //调用方法，获取全部码值
		this.status = this.code['MarketingStatus'];
		this.sexOpt = this.code["SexOpt"];//性别
		this.followStatus = this.code['FollowUpStatus'];//跟进状态
		this.marketingCampaignId = this.routeInfo.snapshot.params['marketingCampaignId'];
		let ob = this.httpService.queryMarketingCampaignById({ marketingCampaignId: this.marketingCampaignId })
		ob.subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.totalMessage = data;
				this.fileList1 = data.marketingContentFileList;//营销活动说明
				this.fileList2 = data.marketingTargetFileList;//营销目标
				this.marketingProgramId = data.marketingProgramId;
				if (this.totalMessage.channelList) {
					for (var i = 0; i < this.totalMessage.channelList.length; i++) {
						this.channelReleaseMsg = this.totalMessage.channelList[i].channelReleaseMsg;
					}
				}
				if (data.prdAndPromRelList) {
					this.prdAndPromRelList = data.prdAndPromRelList || [];//促销手段和关联产品
					this.prdAndPromRelList.forEach((element, i) => {
						let arr = [];
						if (element.productList.length > 0) {
							for (let item of element.productList) {
								let array = {}
								array['productId'] = item.productId,
									array['productName'] = item.productName,
									arr.push(array)
							}
							this.productList = arr;
						}
					});
				}
				this.paramFollow = {
					channelReleaseMsg: this.channelReleaseMsg,
					productList: this.productList,
				}
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示详细', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示详细', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示详细', detail: '调用服务失败' });
		})
	}

	//初始化
	ngOnInit() {
		this.commfunc.clearSession('flagIndex');
		// this.marketingCampaignId = this.routeInfo.snapshot.params['marketingCampaignId'];
		this.workId = this.routeInfo.snapshot.params['workId'];
		this.marketingCampaignName = this.routeInfo.snapshot.params['marketingCampaignName'];
		this.dealWithBean.marketingCampaignId = this.marketingCampaignId;
		this.dealWithBean.workId = this.workId;
		this.dealWithBean.countNum = 10;
		this.dealWithBean.pageNum = 1;
		this.queryClick();
	}
	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}
	//跟进客户列表查询
	queryClick() {
		let ob = this.httpService.queryMarketingCampaignCustList(this.dealWithBean);
		ob.subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.items1 = data.marketingTargetList;
				this.totalNum = data.totalNum;
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示跟进', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示跟进', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示跟进', detail: '调用服务失败' });
		})
	}

	//查看
	followDetail(car) {
		this.headerTitle = "查看详情";
		this.inValue = { list: car, workId: this.dealWithBean.workId, marketingCampaignId: this.marketingCampaignId, paramFollow: this.paramFollow, marketingCampaignName: this.marketingCampaignName, marketingContentFileList: this.fileList1 };
		this.display = true;
	}

	//返回的值
	followUpDetailCall(param) {
		this.display = false;
		this.queryClick();
	}

	//返回的值
	outValue(param) {
		this.display = param.display;
	}


	//分页
	paginate(event) {
		this.dealWithBean.countNum = event.rows * 1;
		let currentPage = event.page + 1;
		this.dealWithBean.pageNum = currentPage;
		this.queryClick();
	}
	//点击页面营销方案中的查看详情按钮跳转营销方案详情页面
	btnDetail() {
		this.router.navigate(['/pages/tzb/custom/marketing/marketing-project/marketing-program-detail', { marketingProgramId: this.marketingProgramId }])
	}
	//下载按钮
	down(temp) {
		let name = temp.fileName;
		let path = temp.fileUrl;
		open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(name) + "&fileUrl=" + path);
	}
	//返回按钮
	returnBtn() {
		setTimeout(() => {
			window.history.go(-1);
		}, 500)
	}
	compileStr(cust){
		return this.commfunc.compileStr(cust)
	  }
}
